import React, { useState } from 'react'
import { graphql } from 'gatsby'
import styled from 'styled-components'

import Layout from '../components/Layout'
import PostTags from '../components/postTags'
import { PostWrapper } from '../components/Postwrapper'

import CryptoJS from 'crypto-js'
import {
  getSessionPassword,
  getQueryPassword,
  deleteSessionCookie
} from '../utils/passwordProtectUtils.js'
import PasswordProtect from "../components/PasswordProtect"

const StyledTag = styled.span`
  font-style: italic;
`

export default function BlogPost({ data }) {
  const post = data.markdownRemark;
  const [decrypted, setDecrypted] = useState('');

  // Check for and decrypt any password protected content
  if (post.fields.encrypted && ! decrypted) {
    try {
      let passwordCandidate = getQueryPassword(window.location) || getSessionPassword();
      passwordCandidate = !passwordCandidate ? 'fail' : passwordCandidate;
      setDecrypted(CryptoJS.AES.decrypt(post.html, passwordCandidate).toString(CryptoJS.enc.Utf8));
    } catch(e) {
      if ( e && e.message && e.message === 'Malformed UTF-8 data' ) {
        // Remove cookie if set
        deleteSessionCookie();
        // Return the password protect module component if unable to get password or decrypt content
        return (
          <PasswordProtect>
            <div></div>
          </PasswordProtect>
        )
      }
    }
  }

  return (
    <Layout>
      <PostWrapper>
        <span className="datetime">{post.frontmatter.date}</span>
        <h1>{post.frontmatter.title}</h1>
        <div dangerouslySetInnerHTML={{ __html: decrypted ? decrypted : post.html }} />
        <hr />
        <p>
          <StyledTag>Tags</StyledTag>: <PostTags tags={post.frontmatter.tags} />
        </p>
      </PostWrapper>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      fields {
        slug
        encrypted
      }
      html
      frontmatter {
        title
        date(formatString: "DD MMMM, YYYY")
        tags
      }
    }
  }
`
